.scrOff {
  position: static;
}

.scrOn {
  position: fixed;
}

.popup_wrap {
  .contents {
    padding: 10px 20px;

    &.contents_sm {
      padding: 13px 20px;
    }

    .title_area {
      margin-bottom: 50px;
    }

    .box {
      margin-bottom: 35px;

      .input_area {
        margin-bottom: 21px;
      }
    }
  }
}

.box_error {
  padding-top: 160px;

  .error_img {
    width: 56px;
    height: 56px;
    text-align: center;
    margin: 0 auto 20px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .error_msg {
    font-size: 15px;
    color: var(--text-01);
    letter-spacing: -0.65px;
    font-family: var(--font2);
    line-height: 24px;
    text-align: center;
  }

  .error_desc {
    font-size: 13px;
    line-height: 20px;
    letter-spacing: -0.33px;
    text-align: center;
    color: #737183;
    margin-top: 8px;
  }
}

.box_loading {
  padding-top: 131px;

  .loading_img {
    width: 200px;
    height: 90px;
    text-align: center;
    margin: 0 auto 20px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .loading_msg {
    font-size: 15px;
    color: var(--color-text-default);
    letter-spacing: -0.38px;
    font-family: var(--font2);
    line-height: 23px;
    text-align: center;
  }

  .loading_desc {
    font-size: 13px;
    line-height: 20px;
    letter-spacing: -0.33px;
    text-align: center;
    color: #737183;
    margin-top: 8px;
  }
}

.box_empty {
  padding-top: 160px;

  .empty_img {
    width: 56px;
    height: 56px;
    text-align: center;
    margin: 0 auto 20px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .empty_msg {
    font-size: 15px;
    color: var(--color-text-default);
    letter-spacing: -0.38px;
    font-family: var(--font2);
    line-height: 23px;
    text-align: center;
  }

  .empty_desc {
    font-size: 13px;
    line-height: 20px;
    letter-spacing: -0.33px;
    text-align: center;
    color: #737183;
    margin-top: 8px;
  }

  &.box_empty_sm {
    padding-top: 130px;
  }
}

.box_updating {
  padding-top: 200px;

  .updating_img {
    width: 26px;
    height: 26px;
    text-align: center;
    margin: 0 auto 20px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .updating_msg {
    display: block;
    font-size: 12px;
    line-height: 1.58;
    letter-spacing: -0.3px;
    text-align: center;
    color: var(--text-04);
  }
}

.popup {
  position: fixed;
  width: 314px;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;

  .popup-header {
    position: relative;
    height: 60px;

    h2 {
      font-size: 15px;
      letter-spacing: -.75px;
      line-height: 60px;
      text-align: center;
      font-family: var(--font1);
      font-weight: bold;
      color: #37354e;
      margin-bottom: 0;
    }

    .btn-close {
      position: absolute;
      display: inline-block;
      top: 20px;
      right: 16px;
    }
  }

  .popup-body {
    padding: 0 18px 72px 20px;

    .title {
      font-family: var(--font1);
      font-size: 15px;
      letter-spacing: -.75px;
      color: #37354e;
      text-align: center;
      line-height: 22px;
      margin-bottom: 15px;
      font-weight: bold;
    }

    > .desc {
      font-size: 13px;
      line-height: 20px;
      letter-spacing: -0.65px;
      color: var(--text-01);
      text-align: center;
    }

    .desc-sm {
      font-family: var(--font1);
      font-size: 12px;
      line-height: 14px;
      letter-spacing: -0.6px;
      text-align: center;
      color: var(--text-03);
      margin-top: 13px;
    }
  }

  .popup-footer {
    position: absolute;
    width: 100%;
    height: 42px;
    bottom: -1px;
    left: 0;

    .confirm-area {

      .btn-full {
        width: 100% !important;
        background-color: #6839d3;

        &.pre {
          background-color: #47209e;
        }

        &.dim {
          background-color: #b39be9;
        }
      }

      a {
        display: inline-block;
        float: left;
        width: 50%;
        text-align: center;
        color: #ffffff;
        line-height: 42px;
        letter-spacing: -.6px;
        font-size: 12px;
        font-family: var(--font1);

        &.left {
          background-color: #afaeb8;

          &.pre {
            background-color: var(--text-02);
          }

          &.dim {
            background-color: #d0cfde;
          }
        }

        &.right {
          background-color: #6839d3;

          &.pre {
            background-color: #47209e;
          }

          &.dim {
            background-color: #b39be9;
          }
        }
      }
    }
  }
}

/*
    팝업 처리
*/
.popup-layer {
  display: none;

  &.popup-sm {
    width: 270px;

    .popup {
      width: 270px;
    }

    .desc {
      padding-top: 29px;
    }
  }
}

.popup-layer.active {
  display: block
}

.popup-dim {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .7);
  top: 0;
  left: 0;
  z-index: 1000;
}

.popup-normal {
  position: fixed;
  width: 314px;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;

  .popup-header {
    position: relative;
    height: 60px;

    h2 {
      font-size: 15px;
      letter-spacing: -.75px;
      line-height: 60px;
      text-align: center;
      font-family: var(--font1);
      font-weight: bold;
      color: #37354e;
    }

    .btn-close {
      position: absolute;
      display: inline-block;
      top: 20px;
      right: 16px;
    }
  }

  .popup-body {
    padding: 24px 18px 0 20px;

    .title {
      font-family: var(--font1);
      font-size: 15px;
      letter-spacing: -.75px;
      color: #37354e;
      text-align: center;
      line-height: 22px;
      margin-bottom: 15px;
      font-weight: bold;
    }

    .rq-box {
      label {
        display: inline-block;
        font-family: var(--font2);
        line-height: 40px;
        font-size: 13px;
        letter-spacing: -.9px;
        margin-right: 28px;
      }

      input {
        display: inline-block;
        width: 140px;
        height: 40px;
        border-radius: 4px;
        border: solid 1px #e2e3e6;
        color: #37354e;
        font-size: 13px;
        line-height: 40px;
        font-family: var(--font1);
        text-align: right;
        margin-right: 5px;
        padding: 0 10px;

        &:focus {
          outline: none;
        }
      }

      .won {
        display: inline-block;
        font-family: var(--font2);
        line-height: 40px;
        font-size: 13px;
        margin-right: 8px;
      }

      a {
        display: inline-block;

        i {
          transform: translate(0, 8px);
        }
      }
    }

    > .desc {
      font-size: 13px;
      line-height: 18px;
      letter-spacing: -0.33px;
      margin-bottom: 13px;
    }
  }

  .popup-footer {
    position: absolute;
    width: 100%;
    height: 42px;
    bottom: 0;
    left: 0;

    .confirm-area {

      .btn-full {
        width: 100% !important;
        background-color: #6839d3;
      }

      a {
        display: inline-block;
        float: left;
        width: 50%;
        text-align: center;
        color: #ffffff;
        line-height: 42px;
        letter-spacing: -.6px;
        font-size: 12px;
        font-family: var(--font1);

        &.left {
          background-color: #afaeb8;
        }

        &.right {
          background-color: #6839d3;
        }
      }
    }
  }

  &.required-performance {
    height: 204px;

    .popup-body {
      padding: 8px 18px 0 20px;

      .rq-box {
        label {
          margin-right: 11px;
          cursor: default;
          font-weight: 500;
        }

        input {
          position: absolute;
          top: 70px;
          right: 20px;
          border-radius: 0;
          border: 0;
          border-bottom: 1px solid #e2e3e6;
          margin-right: 0;
          width: 185px;
          padding: 0 20px;
        }

        .won {
          position: absolute;
          right: 15px;
        }
      }

      .desc {
        font-size: 11px;
        line-height: 32px;
        letter-spacing: -.28px;
        text-align: right;
        color: #9b9aa6;

        span {
          font-family: var(--font1);
          letter-spacing: -.11px;
        }
      }
    }
  }

  &.caution {
    padding-bottom: 67px;

    .popup-body {
      padding: 8px 18px 0 20px;

      > ul {
        border-bottom: 1px solid #e3e4e8;

        .list {
          padding-left: 0;
          margin-bottom: 18px;

          .title {
            display: block;
            font-size: 13px;
            font-weight: bold;
            line-height: 20px;
            letter-spacing: -0.33px;
            margin-bottom: 6px;
            color: var(--text-01);
            text-align: left;
          }

          .title-sm {
            display: block;
            font-family: var(--font1);
            color: var(--text-01);
            font-size: 13px;
            line-height: 20px;
            letter-spacing: -.33px;
            margin-bottom: 0;
            padding-top: 0;
          }

          .desc {
            display: block;
            font-size: 12px;
            color: var(--text-03);
            letter-spacing: -.3px;
            line-height: 20px;
          }

          &::before {
            content: none;
          }
        }

      }

    }

    .popup-footer {
      width: 100%;
      height: 67px;
      padding: 13px 20px 0 36px;

      .caution-area {
        position: relative;

        p {
          font-size: 11px;
          letter-spacing: -.55px;
          line-height: 18px;
          color: var(--text-03);

          i {
            position: absolute;
            left: -15px;
            top: 3px;
          }
        }
      }
    }
  }

  &.annual_fee {
    padding-bottom: 30px;

    .popup-body {
      padding: 0 18px 0 20px;

      .table {
        position: relative;

        thead {
          tr {
            th {
              font-family: var(--font1);
              border-top: 0;
              border-right: 1px solid #f5f6f8;
              height: 36px;
              line-height: 36px;
              color: #737183;
              letter-spacing: -.6px;
              background-color: transparent;

              &:last-child {
                border-right: 0;
              }
            }
          }
        }

        tbody {
          tr {
            td {
              font-family: var(--font1);
              height: 42px;
              line-height: 42px;
              border-right: 1px solid #f5f6f8;
              border-top: 1px solid #f5f6f8;

              &:first-child {
                color: #737183;
                text-align: left;
                padding-left: 8px;
              }

              &:last-child {
                border-right: 0;
              }

              &.won {
                text-align: center;
                /*padding-right: 8px;*/

                &::after {
                  content: '원';
                  font-family: var(--font2);
                  font-size: 13px;
                  line-height: 19px;
                }
              }
            }
          }
        }

        &::after {
          content: '';
          position: absolute;
          width: 276px;
          border-top: 1px solid #37354e;
          top: 0;
          left: 0;
        }
      }
    }
  }

  &.automatic-payment-details {
    width: 270px;

    .popup-body {
      padding: 8px 18px 10px 20px;

      li {
        position: relative;
        margin-bottom: 7px;

        span {
          float: left;

          &.title {
            width: 120px;
            font-family: var(--font2);
            font-weight: 500;
            font-size: 12px;
            line-height: 17px;
            letter-spacing: normal;
            text-align: left;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
          }

          &.won {
            width: 120px;

            font-size: 13px;
            line-height: 17px;
            text-align: right;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            position: absolute;
            right: 0;
            top: 0;

            &::after {
              content: '원';
              font-family: var(--font2);
              font-size: 13px;
              line-height: 17px;
            }
          }
        }

        &:last-child {
          border-bottom: 1px solid #e3e4e8;
        }
      }

      .empty {
        margin-top: 36px;
        margin-bottom: 82px;

        .img {
          width: 42px;
          height: 42px;
          margin: 0 auto 12px;

          img {
            width: 100%;
            height: 100%;
          }
        }

        p {
          font-size: 12px;
          line-height: 18px;
          letter-spacing: -0.6px;
          text-align: center;
          color: #737183;
        }
      }

      .tip {
        position: relative;
        font-size: 11px;
        line-height: 1.64;
        letter-spacing: -0.55px;
        text-align: left;
        color: var(--text-03);
        vertical-align: middle;
        padding-left: 16px;

        i {
          position: absolute;
          margin-right: 4px;
          top: 3px;
          left: 0;
        }
      }
    }
  }

  &.input-income-information {
    .popup-body {
      padding: 0 18px 71px 20px;

      .label-box {
        margin-bottom: 16px;

        label {
          &:first-child {
            margin-right: 24px;
          }
        }
      }

      .income_input_box {
        label {
          display: inline-block;
          font-family: var(--font2);
          font-size: 13px;
          font-weight: 500;
          line-height: 40px;
          letter-spacing: -0.65px;
        }

        input {
          display: inline-block;
          width: 140px;
          position: absolute;
          right: 50px;
          text-align: right;
        }

        .won {
          position: absolute;
          right: 20px;
          bottom: 80px;
          font-family: var(--font2);
          font-size: 13px;
          font-weight: 500;
          line-height: 19px;
          letter-spacing: normal;
        }
      }
    }
  }

  &.ex-tooltip {
    padding-bottom: 30px;

    .popup-body {
      .list {
        font-size: 13px;
        line-height: 20px;

        &::before {
          top: 9px;
        }
      }
    }
  }

  &.select_month {
    width: 240px;
    height: 222px;

    .popup-body {
      padding: 0 20px;
      margin-top: -10px;

      ul {
        li {
          width: 100%;
          height: 52px;
          border-bottom: 1px solid #f5f6f8;

          a {
            display: block;
            font-size: 14px;
            line-height: 52px;
            text-align: center;
            color: var(--text-01);
          }

          &.active {
            a {
              position: relative;
              font-weight: bold;
              color: var(--text-point-color);

              &::after {
                content: '';
                display: inline-block;
                position: absolute;
                right: 4px;
                top: 14px;
                background-image: url('../../img/sprite/common/month_check@2x.png');
                background-repeat: no-repeat;
                background-size: cover;
                width: 20px;
                height: 20px;
              }
            }
          }

          &:last-child {
            border-bottom: 0;
          }
        }
      }
    }
  }
}

.toast-popup {
  display: none;
  position: fixed;
  width: 131px;
  height: 53px;
  text-align: center;
  background-color: rgba(55, 53, 78, .9);
  border-radius: 6px;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -90%);
  z-index: 111111;

  p {
    color: #ffffff;
    font-size: 11px;
    line-height: 53px;
    letter-spacing: -.28px;

    img {
      display: inline-block;
      width: 13px;
      height: 13px;
      margin-right: 4px;
      transform: translate(0px, -1px);
    }
  }

  &.alert-setting {
    width: calc(100% - 60px);
    height: auto;
    background-color: rgba(55, 53, 78,.9);
    top: 94%;
    padding: 15px 0;

    p {
      font-size: 12px;
      line-height: 18px;
      font-weight: normal;
      letter-spacing: -0.3px;
      text-align: center;
      color: #ffffff;
    }
  }

  &.position-top {
    width: auto;
    min-width: 234px;
    height: auto;
    background-color: var(--text-01);
    top: 88%;
    padding: 16px 30px;

    p {
      font-size: 12px;
      line-height: 17px;
      letter-spacing: -0.3px;
      text-align: center;
      color: #ffffff;
      white-space: nowrap;
    }
  }

  &.position-bottom {
    width: auto;
    min-width: 234px;
    height: auto;
    background-color: var(--text-01);
    top: 92%;
    padding: 16px 30px;

    p {
      font-size: 12px;
      line-height: 17px;
      letter-spacing: -0.3px;
      text-align: center;
      color: #ffffff;
      white-space: nowrap;
    }
  }

  &.terms_of_use_detail {
    width: 215px;
    height: 63px;
    background-color: var(--text-01);
    top: 94%;
    padding: 14px 0 15px;

    p {
      font-size: 11px;
      line-height: 16px;
      font-weight: normal;
      letter-spacing: -0.28px;
      text-align: center;
      color: #ffffff;
    }
  }
}

@media all and (max-height: 500px) {
  .box_error {
    padding-top: 50px;
  }
}

.modal-backdrop.show {
  opacity: 0.7;
}

.modal {
  .modal-dialog {
    width: 295px;
    max-width: 295px !important;
    margin: 0 auto;

    .modal-content {
      position: relative;
      width: 295px;
      text-align: center;
      padding: 30px 0;
      border: 0;
      border-radius: 6px;
      box-shadow: none;

      .btn-close {
        position: absolute;
        display: block;
        width: 22px;
        height: 22px;
        background-image: url(../../img/common/btn-popup-close.svg);
        background-size: cover;
        right: 16px;
        top: 20px;
      }

      .tit {
        font-family: var(--font3);
        font-size: 15px;
        font-weight: bold;
        line-height: 22px;
        letter-spacing: -0.75px;
        margin-bottom: 12px;
      }

      .txt {
        font-size: 13px;
        line-height: 20px;
        letter-spacing: -0.33px;
      }
    }

    .modal-footer {
      padding: 0;
      margin: 0;

      > :not(:last-child) {
        margin-right: 0;
      }

      .btn-footer {
        display: inline-block;
        float: left;
        width: 50%;
        text-align: center;
        color: #ffffff;
        line-height: 42px;
        letter-spacing: -.6px;
        font-size: 12px;
        font-family: var(--font1);
        background-color: var(--blurple);
        margin-left: 0;

        &.bg-grey {
          background-color: var(--text-03);
        }

        &.single {
          width: 100% !important;
          background-color: var(--blurple);
        }
      }
    }
  }

  &.modal-sm {
    .modal-dialog {
      width: 270px;
      max-width: 270px !important;

      .modal-content {
        width: 270px;

        .modal-header {
          border-bottom: 0;
        }
      }
    }
  }

  &.modal-md {
    .modal-dialog {
      width: 315px;
      max-width: 315px !important;

      .modal-content {
        width: 315px;

        .modal-header {
          border-bottom: 0;
        }
      }
    }
  }

  &.modal-common {
    .modal-dialog {
      .modal-content {
        border-radius: 0;
        text-align: left;
        padding: 20px 0 0;

        .modal-header {
          padding: 0;
          margin-bottom: 18px;
          justify-content: center;

          .title {
            font-family: var(--font3);
            font-size: 15px;
            font-weight: 800;
            font-stretch: normal;
            font-style: normal;
            line-height: 19px;
            letter-spacing: -0.75px;
            text-align: center;
            color: var(--text-01);
          }
        }

        .modal-body {
          padding: 0 30px 30px;

          .txt {
            font-size: 13px;
            line-height: 20px;
            letter-spacing: -0.65px;
            text-align: center;
            color: var(--text-01);
          }

          .txt-sm {
            font-size: 12px;
            line-height: 18px;
            letter-spacing: -0.6px;
            text-align: center;
            color: var(--text-03);
            margin-top: 10px;
          }
        }
      }
    }
  }
}

